export default {
	onLoad(options){
		console.log(options)
		this.price = options.price

		this.price = Number(this.price).toFixed(2)

		this.subString = options.subString

	},
	data () {
		return {
			price: '', //输入的金额
			currentIndex: 0, //下标
			cheque: '',  //抬头
			duty: '', //税号
			email: '', //邮箱
			recipients: '',//收件人
			phone: '',
			address: '',
			//接口参数
			subString: '',
			//正则
			dutyFlag: '',
			emailFlag: '',
			phoneFlag: '',
			
		}
	},
	methods:{
		handleCurrentIndex (number) {
			this.currentIndex = number
		},
		async invioceSubmit () {
			
			if (this.dutyFlag) {
				uni.showToast({
					icon: 'none',
					title: '请检查税号是否输入正确'
				})
				return
			}
			
			if (this.emailFlag) {
				uni.showToast({
					icon: 'none',
					title: '请检查邮箱是否输入正确'
				})
				return
			}
			
			if (this.currentIndex === 0) {
				if (this.cheque == "" || this.duty == "" || this.email == "" || this.dutyFlag) {
					uni.showToast({
						icon: 'none',
						title: '请检查输入的内容'
					})
					return
				}
				
				
				
				let res = await this.$fetch(this.$api.invoiceApply,{totalAmount:this.price,dutyParagraph:this.duty,invoiceRise:this.cheque,email:this.email,type:0,orderIds:this.subString},"POST")
				
				if (res.code == 0) {
					uni.showToast({
						icon: 'success',
						title: '申请成功'
					})
					uni.setStorageSync('backFlag',true)
					setTimeout(() =>{
						uni.navigateBack({
							delta:1
						})
						
						// uni.redirectTo({
						// 	url: '../applyInvioce/applyInvioce',
						// 	success(){
						// 	    let page = getCurrentPages().pop();  //跳转页面成功之后
						// 	    if (!page) return;  
						// 	    page.onLoad(); //如果页面存在，则重新刷新页面
						// 	}
						// });
					},1000)
				}else {
					uni.showToast({
						icon: 'none',
						title: res.msg
					})
				}
			}
			if (this.currentIndex === 1) {
				if(this.phoneFlag) {
					uni.showToast({
						icon:'none',
						title: '您输入的手机号码有误'
					});
					return
				}
				
				if (this.cheque == "" || this.duty == "" || this.recipients == "" || this.phone == "" || this.address == ""){
					uni.showToast({
						icon:'none',
						title: '请检查您输入的内容是否有误'
					});
					return
				}
				
				
				let res = await this.$fetch(this.$api.invoiceApply,{totalAmount:this.price,dutyParagraph:this.duty,invoiceRise:this.cheque,type:1,orderIds:this.subString,address:this.address,mobile:this.phone,name:this.recipients},"POST")
				
				if (res.code == 0) {
					uni.showToast({
						icon: 'success',
						title: '申请成功'
					})
					
					uni.setStorageSync('backFlag',true)
					setTimeout(() =>{
						uni.navigateBack({
							delta:1
						})
						
						// uni.redirectTo({
						// 	url: '../historyInvioce/historyInvioce',
						// 	success(){
						// 	    let page = getCurrentPages().pop();  //跳转页面成功之后
						// 	    if (!page) return;  
						// 	    page.onLoad(); //如果页面存在，则重新刷新页面
						// 	}
						// });
						
						
					},1000)
				} else {
					uni.showToast({
						icon: 'none',
						title: res.msg
					})
				}
			}
		},
		//验证税号
		checkDuty(){
			let reg = /^[A-Z0-9]{15}$|^[A-Z0-9]{17}$|^[A-Z0-9]{18}$|^[A-Z0-9]{20}$/;
			if (reg.test(this.duty)){
				this.dutyFlag = false
				
				return true
			} else {
				this.dutyFlag = true
				
				return false
			}
			
		},
		//验证邮箱
		checkEmail(){
			let reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
			if(reg.test(this.email)){
				this.emailFlag = false
				return true
			} else {
				this.emailFlag = true
				return false
			}
		},
		//验证手机
		checkPhone(){
			let reg = /^1[3456789]\d{9}$/;
			if (reg.test(this.phone)) {
				this.phoneFlag = false
				
				return true
			} else {
				this.phoneFlag = true
	
				return false
			}
		}
	}
}
